.mainWrapper {
    display: flex;
    justify-content: center;
    width: 100%;
    min-height: 100vh;
    background-color: lightpink;
}

.mainBlock {
    display: flex;
    justify-content: space-around;
    align-self: center;
    width: 85%;
    height: 50vh;
}

.introduceBlock {
    align-self: center;
    background-color: lightgray;
}

.introduceItem {
    display: flex;
    font-weight: bold;
    font-size: 30px;
}

.avatarBlock {
    width: 50vh;
    height: 100%;
    background-color: lightgray;
}

.mainSkillsBlock {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    min-height: 100vh;
    background-color: #cbf7f7;
}

.mainSkillsBlockChild {
    display: flex;
    flex-direction: column;
    align-self: center;
    width: 90%;
    margin: 70px 0 70px 0;
}

.skillsDescription {
    width: 100%;
    margin-bottom: 60px;
    text-align: center;
    font-weight: bold;
    font-size: 30px;
}

.skillsDescription span {
    background-color: lightgray;
}

.mySkills {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    min-height: 100vh;
}

.mainProjectsBlock {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    min-height: 100vh;
    background-color: lightpink;
}

.mainProjectsBlockChild {
    display: flex;
    flex-direction: column;
    align-self: center;
    width: 90%;
    height: 80%;
    margin: 70px 0 70px 0;
}

.projectsDescription {
    width: 100%;
    text-align: center;
    font-weight: bold;
    font-size: 30px;
    margin-bottom: 60px;
}

.myProjects {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    width: 100%;
    min-height: 100vh;
}

.mainRemoteJobBlock {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 300px;
    background-color: white;
}

.mainRemoteJobBlockChild{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    align-self: center;
    font-weight: bold;
    font-size: 30px;
    width: 75%;
    height: 50%;
}


.mainRemoteJobBlockChild button {
    width: 200px;
    height: 50px;
    font-size: 20px;
    font-weight: bold;
    background-color: gray;
}

.mainContactsBlock {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 700px;
    background-color: lightpink;
}

.mainContactsBlockChild{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    align-self: center;
    font-weight: bold;
    font-size: 30px;
    width: 75%;
    height: 80%;
}

.mainContactsBlockChild span {
    background-color: lightgray;
}

.contactsFormBlock {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    width: 50%;
    height: 50%;
}
.contactsFormBlock input {
    height: 30px;
    background-color: lightgray;
}

.contactsFormBlock textarea {
    height: 180px;
    background-color: lightgray;
    resize: none;
}

.mainContactsBlockChild button {
    width: 200px;
    height: 50px;
    font-size: 20px;
    font-weight: bold;
    background-color: gray;
}

@media (max-width: 769px){
    .mainWrapper {
        width: 100%;
        height: 400px;
        background-color: lightpink;
    }

    .mainBlock {
        flex-direction: column;
        justify-content: start;
        align-items: center;
        width: 100%;
        height: 100%;
    }

    .introduceBlock {
        align-self: flex-start;
        margin: 5px 0 0 10px;
    }

    .introduceItem {
        font-size: 25px;
    }

    .avatarBlock {
        width: 300px;
        height: 55%;
        margin-top: 30px;
    }


    .skillsDescription {
        display: flex;
        justify-content: center;
        width: 100%;
        min-height: 5vh;
        margin-bottom: 70px;
        font-size: 25px;
    }

    .mySkills {
        justify-content:space-around;
    }

    .mainProjectsBlock {
        width: 100%;
        min-height: 100vh;
    }

    .mainProjectsBlockChild {
        width: 100%;
        height: 100%;
    }

    .projectsDescription {
        font-size: 25px;
    }

    .myProjects {
       justify-content: space-around;
    }

    .mainRemoteJobBlock {
        height: 500px;
    }

    .mainRemoteJobBlockChild{
        justify-content: space-evenly;
        width: 100%;
        height: 100%;
    }

    .mainRemoteJobBlockChild span {
        width: 300px;
        text-align: center;
        background: none;
    }



    .mainContactsBlock {
        height: 650px;

    }

    .mainContactsBlockChild{
        display: flex;
        justify-content: space-evenly;
        font-size: 25px;
        width: 100%;
        height: 100%;
    }


    .contactsFormBlock {
        display: flex;
        justify-content: start;
        flex-direction: column;
        width: 360px;
        height: 270px;
    }
    .contactsFormBlock input {
        margin-bottom: 10px;
    }
}